import React, { useEffect } from 'react';
import styles from './UniaLubelska.module.scss';
import { Link } from 'react-scroll';
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

const UniaLubelska7 = () => {
    const wrapper = React.useRef(null);
    const content = React.useRef(null);
    useEffect(() => {
        gsap.registerPlugin(ScrollTrigger);
        const wrap = wrapper.current.children;
        const tl = gsap.timeline();

        tl.fromTo(wrap,0.6,{autoAlpha : 0,x : -100},{autoAlpha : 1,x : 0,scrollTrigger : {
            trigger : '#trigger7',
            start : 'top center',
            id : 'trigger7'
        }})
    });
    return (
        
<div id="trigger7" ref={wrapper} className={styles.union2}>
<h1>Ważne wydarzenia z XVII w</h1>
<div ref={content} className={styles.content}>
        <ul className={styles.smallerUl}>
            <li>1604–1606 – odbyła się pierwsza dymitriada, wyprawa zbrojna wojsk kilku polskich magnatów na Rosję w celu 	osadzenia na tronie moskiewskim Dymitra Samozwańca</li>
            <li>1606–1609 – rokosz Zebrzydowskiego</li>
            <li>28 maja 1609 – Zygmunt III Waza przeniósł siedzibę królestwa z Krakowa do Warszawy</li>
            <li>23 marca 1613 – Rzeczpospolita zawarła sojusz z Austrią</li>
            <li>1617–1618 – wojna polsko-szwedzka</li>
            <li>1621 – bitwa pod Chocimiem</li>
            <li>1622 – Szwedzi ponownie najechali na Inflanty</li>
            <li>1625 – trzeci najazd Szwecji na Inflanty</li>
            <li>1626–1629 – wojna polsko-szwedzka</li>
            <li>19 lipca 1655 – król Karol X wypowiedział wojnę Rzeczypospolitej – początek potopu szwedzkiego</li>
            <li>25 lipca 1655 – Szwedzi zajęli Wielkopolskę</li>
            <li>8 września 1655 – Szwedzi zajęli Warszawę</li>
            <li>wrzesień–październik 1655 – oblężenie Krakowa</li>
        </ul>
<div className={styles.img7}></div>
<Link
    smooth={true}
    spy={true}
    to="union8"
    >
    <button>Dalej</button>
</Link>
</div>
</div>
    )
}

export default UniaLubelska7;
