import React,{useRef} from 'react';
import styles from './HomePage.module.scss';
import video from './video2.mp4';
import UniaLubelska from '../UniaLubelska/UniaLubelska';
import UniaLubelska2 from '../UniaLubelska/UniaLubelska2';
import UniaLubelska3 from '../UniaLubelska/UniaLubelska3';
import UniaLubelska4 from '../UniaLubelska/UniaLubelska4';
import UniaLubelska5 from '../UniaLubelska/UniaLubelska5';
import UniaLubelska6 from '../UniaLubelska/UniaLubelska6';
import UniaLubelska7 from '../UniaLubelska/UniaLubelska7';
import UniaLubelska8 from '../UniaLubelska/UniaLubelska8';
import UniaLubelska9 from '../UniaLubelska/UniaLubelska9';
import UniaLubelska10 from '../UniaLubelska/UniaLubelska10';
import { Link } from 'react-scroll';

const HomePage = () => {
    return (
        <>
        <div id="main" className={styles.home}>
            <video autoPlay loop muted>
                <source src={video} type="video/mp4" />
            </video> 
            <div>
                <h1>Rzeczpospolita</h1>
                <h1>Obojga Narodów</h1>
                <Link
                        smooth={true}
                        spy={true}
                        to="UniaLubelska"
                    >
                       <button>Przejdź Dalej</button>
                    </Link>
                <h2>Krystian Zieja Maciej Papież Dawid Kosakowski</h2>
            </div>          
        </div>
        <div id="UniaLubelska" className={styles.unia}>
            <UniaLubelska />
        </div>
        <div id="union2" className={styles.unia}>
            <UniaLubelska2 />
        </div>
        <div id="union3" className={styles.unia}>
            <UniaLubelska3 />
        </div>
        <div id="union5" className={styles.unia}>
            <UniaLubelska5 />
        </div>
        <div id="union4" className={styles.unia}>
            <UniaLubelska4 />
        </div>
        <div id="union6" className={styles.unia}>
            <UniaLubelska6 />
        </div>
        <div id="union7" className={styles.unia}>
            <UniaLubelska7 />
        </div>
        <div id="union8" className={styles.unia}>
            <UniaLubelska8 />
        </div>
        <div id="union9" className={styles.unia}>
            <UniaLubelska9 />
        </div>
        <div id="union10" className={styles.unia}>
            <UniaLubelska10 />
        </div>
        </>
    )
}

export default HomePage;

