import React, { useEffect } from 'react';
import styles from './UniaLubelska.module.scss';
import { Link } from 'react-scroll';
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

const UniaLubelska6 = () => {
    const wrapper = React.useRef(null);
    const content = React.useRef(null);
    useEffect(() => {
        gsap.registerPlugin(ScrollTrigger);
        const wrap = wrapper.current.children;
        const tl = gsap.timeline();

        tl.fromTo(wrap,0.6,{autoAlpha : 0,x : -100},{autoAlpha : 1,x : 0,scrollTrigger : {
            trigger : '#trigger6',
            start : 'top center',
            id : 'trigger6'
        }})
    });
    return (
        
<div id="trigger6" ref={wrapper} className={styles.union2}>
<h1>Ważne wydarzenia z XVI w</h1>
<div ref={content} className={styles.content}>
        <ul>
            <li>1 lipca 1569 – Polska i Litwa zawarły unię lubelską, na mocy którego oba państwa połączyły się w jeden kraj</li>
            <li>7 lipca 1572 – zmarł Zygmunt August – koniec dynastii Jagiellonów</li>
            <li>1572–1573 – okres bezkrólewia</li>
            <li>Panowanie Henryka Walezego (1573–1574)</li>
            <li>1577 – Iwan IV Groźny najechał na Inflanty</li>
            <li>1579–1581 – wojna z Rosją o Inflanty</li>
            <li>27 grudnia 1587 – koronowano Zygmunta Wazę</li>
            <li>1591–1593 – wybuchło powstanie kozackie Krzysztofa Kosińskiego</li>
            <li>6–10 października 1596 – zawarto unię brzeską</li>
            <li>1599 – ukazał się pierwszy pełny przekład Biblii na język polski ks. Jakuba Wujka</li>
        </ul>
<div className={styles.img6}></div>
<Link
    smooth={true}
    spy={true}
    to="union7"
    >
    <button>Dalej</button>
</Link>
</div>
</div>
    )
}

export default UniaLubelska6;
