import React from 'react';
import styles from './Navbar.module.scss';
import { Link } from 'react-scroll';

const Navbar = () => {
    return (
        <div className={styles.navbar}>
            <div className={styles.img}></div>
            <h1><Link
                        smooth={true}
                        spy={true}
                        to="main"
                    >
                        Strona Główna
                    </Link></h1>
            <div className={styles.elements}>
                <h1>
                    <Link
                        smooth={true}
                        spy={true}
                        to="UniaLubelska"
                    >
                        Unia Lubelska
                    </Link>
                </h1>
                <h1>
                    <Link
                        smooth={true}
                        spy={true}
                        to="union3"
                    >
                        Administracja
                    </Link>
                </h1>
            </div>
            <div className={styles.elements2}>
            <h1>
                    <Link
                        smooth={true}
                        spy={true}
                        to="union4"
                    >
                        Geografia
                    </Link>
                </h1>
                <h1>
                    <Link
                        smooth={true}
                        spy={true}
                        to="union6"
                    >
                        Wydarzenia
                    </Link>
                </h1>
            </div>
        </div>
    )
}

export default Navbar;