import React, { useEffect } from 'react';
import styles from './UniaLubelska.module.scss';
import { Link } from 'react-scroll';
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';


const UniaLubelska = () => {
    const wrapper = React.useRef(null);
    useEffect(() => {
        gsap.registerPlugin(ScrollTrigger);
        const wrap = wrapper.current.children;
        const tl = gsap.timeline();

        tl.fromTo(wrap,0.6,{autoAlpha : 0,x : -100},{autoAlpha : 1,x : 0,scrollTrigger : {
            trigger : '#trigger2',
            start : 'top center',
            id : 'trigger2'
        }})
    });
    return (
        <>
        <div id="trigger2" ref={wrapper} className={styles.union}>
            <h1>Przyczyny zawarcia Unii Lubelskiej</h1>
            <div className={styles.content}>
            <ul>
                <li>Wielkie Księstwo Litewskie prowadziło wiele wojen z Księstwem Moskiewskim, ale nie było w stanie samodzielnie przeciwstawić się wrogowi ze wschodu.</li>
                <li>Król Zygmunt August nie posiadł potomka, przez co rosło niebezpieczeństwo wygaśnięcia dynastii Jagiellonów. Władca wówczas zgodził się na zastąpienie unii personalnej, unią realną. </li>
                <li>Litewska szlachta tzw. bojarzy chcieli uzyskać takie przywileje, jakie posiadała Polska szlachta. </li>
                <li>Przywódcy ruchu egzekucyjnego, Mikołaj Sienicki i Rafał Leszczyński postulowali o włączenie Wielkiego Księstwa Litewskiego do Korony.</li>
                <li>W XVI wieku Litwa znacznie ,,zbliżyła'' się do Polski. Zmianie uległ ustrój Litewski, odwzorowany został od polskiego. </li>
                <li>Polska i Litwa potrzebowały silnego sojuszu. Ludność obu krajów rozumiała tą potrzebę, nuncjusz Juliusz Ruggieri pisał ,,Oba narody czują potrzebę ściślejszego związku nie odkryły jeszcze równie dogodnego sposobu połączenia się ''</li>
            </ul>
            <div className={styles.img1}></div>
            <Link
                smooth={true}
                spy={true}
                to="union2"
            >
                <button>Dalej</button>
            </Link>
            </div>
        </div>
        </>
    )
}

export default UniaLubelska;
