import React, { useEffect } from 'react';
import styles from './UniaLubelska.module.scss';
import { Link } from 'react-scroll';
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

const UniaLubelska10 = () => {
    const wrapper = React.useRef(null);
    const content = React.useRef(null);
    useEffect(() => {
        gsap.registerPlugin(ScrollTrigger);
        const wrap = wrapper.current.children;
        const tl = gsap.timeline();

        tl.fromTo(wrap,0.6,{autoAlpha : 0,x : -100},{autoAlpha : 1,x : 0,scrollTrigger : {
            trigger : '#trigger10',
            start : 'top center',
            id : 'trigger10'
        }})
    });
    return (
        
<div id="trigger10" ref={wrapper} className={styles.union2}>
<h1>Ważne wydarzenia z XVIII w (2)</h1>
<div ref={content} className={styles.content}>
        <ul className={styles.smallerUl}>
            <li>6 maja 1787 – Rosja nie zezwoliła na przeprowadzenie reform ustrojowych i wojskowych w Polsce</li>
            <li>październik 1788 – maj 1792 – obrady Sejmu Wielkiego (Czteroletniego)</li>
            <li>1789–1790 – odbył się pierwszy w historii spis powszechny</li>
            <li>3 maja 1791 – uchwalono pierwszą polską konstytucję (Konstytucja 3 maja)</li>
            <li>27 kwietnia 1792 – zawiązano konfederację targowicką</li>
            <li>maj – lipiec 1792 – wojna z Rosją w obronie Konstytucji 3 maja</li>
            <li>22 czerwca 1792 – Stanisław August Poniatowski ustanowił order Virtuti Militari</li>
            <li>18 lipca 1792 – wojska Tadeusza Kościuszki przegrały pod Dubienką</li>
            <li>24 lipca 1792 – Stanisław Poniatowski przystąpił do konfederacji targowickiej</li>
            <li>21 stycznia 1793 – II rozbiór Polsk</li>
            <li>4 kwietnia 1794 – bitwa pod Racławicami wygrana przez wojska Kościuszki</li>
            <li>17 kwietnia 1794 – wybuchło powstanie na Żmudzi</li>
            <li>7 maja 1794 – Tadeusz Kościuszko wydał pod Połańcem uniwersał (zwany połanieckim), w którym zapowiedział reformę agrarną</li>
            <li>10 października 1794 – bitwa pod Maciejowicami: Kościuszko dostał się do niewoli</li>
            <li>24 października 1795 – III rozbiór Polski</li>
        </ul>
<div className={styles.img10}></div>
<Link
    smooth={true}
    spy={true}
    to="main"
    >
    <button>Cofnij</button>
</Link>
</div>
</div>
    )
}

export default UniaLubelska10;
