import React, { useEffect } from 'react';
import styles from './UniaLubelska.module.scss';
import { Link } from 'react-scroll';
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

const UniaLubelska5 = () => {
    const wrapper = React.useRef(null);
    const content = React.useRef(null);
    useEffect(() => {
        gsap.registerPlugin(ScrollTrigger);
        const wrap = wrapper.current.children;
        const tl = gsap.timeline();

        tl.fromTo(wrap,0.6,{autoAlpha : 0,x : -100},{autoAlpha : 1,x : 0,scrollTrigger : {
            trigger : '#trigger5',
            start : 'top center',
            id : 'trigger5'
        }})
    });
    return (
        
<div id="trigger5" ref={wrapper} className={styles.union2}>
<h1>Administracja Litwy</h1>
<div ref={content} className={styles.content}>
<p id="text" className={styles.p2}>
Po reformach z lat 1565–1566 w Wielkim Księstwie wyklarował się prosty, dwustopniowy podział administracyjny. Podstawową jednostką administracyjną był powiat, który był jednocześnie okręgiem sądowym, politycznym (zwoływanie sejmików), jak i wojskowym (pospolite ruszenie)[28]. Od Unii lubelskiej Litwa dzieliła się na dziewięć województw: wileńskie, trockie, połockie, witebskie, smoleńskie, mińskie, mścisławskie, nowogródzkie, brzesko-litewskie. Status województwa miało także Księstwo Żmudzkie, które podobnie jak województwa połockie i mścisławskie składały się z jednego powiatu, pozostałe województwa liczyły od dwóch do czterech powiatów, jedynie wileńskie składało się z pięciu. Poza zwykłym podziałem administracyjnym na obszarze Wielkiego Księstwa Litewskiego występowały także tak zwane księstwa alodialne, czyli kompleksy majątkowe niektórych rodów magnackich, dzierżone na prawie książęcym.
Korona Królestwa Polskiego
W Koronie Królestwa Polskiego podział administracyjny był dużo bardziej skomplikowany. Korona dzieliła się na dwie prowincje Wielkopolskę i Małopolskę.
</p>
<div className={styles.img5}></div>
<Link
    smooth={true}
    spy={true}
    to="union4"
    >
    <button>Dalej</button>
</Link>
</div>
</div>
    )
}

export default UniaLubelska5;
