import React, { useEffect } from 'react';
import styles from './UniaLubelska.module.scss';
import { Link } from 'react-scroll';
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

const UniaLubelska3 = () => {
    const wrapper = React.useRef(null);
    const content = React.useRef(null);
    useEffect(() => {
        gsap.registerPlugin(ScrollTrigger);
        const wrap = wrapper.current.children;
        const tl = gsap.timeline();

        tl.fromTo(wrap,0.6,{autoAlpha : 0,x : -100},{autoAlpha : 1,x : 0,scrollTrigger : {
            trigger : '#trigger3',
            start : 'top center',
            id : 'trigger3'
        }})
    });
    return (
        
<div id="trigger3" ref={wrapper} className={styles.union2}>
<h1>Administracja</h1>
<div ref={content} className={styles.content}>
<p id="text">Rzeczpospolita Obojga Narodów składała się z dwóch części: Korony Królestwa Polskiego (potocznie – Korony) i Wielkiego Księstwa Litewskiego (potocznie – Litwy), które posiadały dużą niezależność przejawiającą się również w administracji terytorialnej.Korona dzieliła się na dwie prowincje: wielkopolską (do której zaliczano również Mazowsze i Prusy Królewskie) oraz małopolską (z Rusią Czerwoną, Podolem, Wołyniem i Ukrainą). W części litewskiej natomiast była tylko jedna prowincja.</p>
<div className={styles.img3}></div>
<Link
    smooth={true}
    spy={true}
    to="union5"
    >
    <button>Dalej</button>
</Link>
</div>
</div>
    )
}

export default UniaLubelska3;
