import React, { useEffect } from 'react';
import styles from './UniaLubelska.module.scss';
import { Link } from 'react-scroll';
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

const UniaLubelska2 = () => {
    const wrapper = React.useRef(null);
    const content = React.useRef(null);
    useEffect(() => {
        gsap.registerPlugin(ScrollTrigger);
        const wrap = wrapper.current.children;
        const tl = gsap.timeline();

        tl.fromTo(wrap,0.6,{autoAlpha : 0,x : -100},{autoAlpha : 1,x : 0,scrollTrigger : {
            trigger : '#trigger',
            start : 'top center',
            id : 'trigger'
        }})
    });
    return (
        
<div id="trigger" ref={wrapper} className={styles.union2}>
<h1>Geneza Unii Lubelskiej i powstanie nowego kraju</h1>
<div ref={content} className={styles.content}>
<p id="text">10 stycznia 1569 roku rozpoczęły się obrady sejmu walnego ,,pod laską'' Stanisława Sędziwoja Czarkowskiego, przybyli posłowie z Polski i Litwy. Podczas negocjacji starano się dojść do kompromisu, jednakże litewscy magnaci, byli sceptyczni w stosunku do zawarcia unii i 1 marca opuścili Lublin przerywając obrady sejmu. Zygmunt August postanowił włączyć ziemie Litwy do Korony. 5 marca włączył Podlasie, 26 maja Wołyń, a 6 czerwca województwa: Kijowskie i Bracławskie. Inkorporacja owych ziem zmusiła przedstawicieli litewskich do powrotu. Po wznowieniu rokowań 7 czerwca, próbowano dojść do zgody. 28 czerwca uchwalono zawarcie nowej unii polsko-litewskiej, a 1 lipca zaprzysiężony został akt. Odtąd oba państwa posiadały wspólny sejm, senat, władcę i monetę. Litwa natomiast zachowała oddzielną armię urzędy i skarb. Narodziło się wówczas potężne państwo, mianowicie Rzeczpospolita Obojga Narodów.</p>
<div className={styles.img2}></div>
<Link
    smooth={true}
    spy={true}
    to="union3"
    >
    <button>Dalej</button>
</Link>
</div>
</div>
    )
}

export default UniaLubelska2;
