import React, { useEffect } from 'react';
import styles from './UniaLubelska.module.scss';
import { Link } from 'react-scroll';
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

const UniaLubelska8 = () => {
    const wrapper = React.useRef(null);
    const content = React.useRef(null);
    useEffect(() => {
        gsap.registerPlugin(ScrollTrigger);
        const wrap = wrapper.current.children;
        const tl = gsap.timeline();

        tl.fromTo(wrap,0.6,{autoAlpha : 0,x : -100},{autoAlpha : 1,x : 0,scrollTrigger : {
            trigger : '#trigger8',
            start : 'top center',
            id : 'trigger8'
        }})
    });
    return (
        
<div id="trigger8" ref={wrapper} className={styles.union2}>
<h1>Ważne wydarzenia z XVII w (2)</h1>
<div ref={content} className={styles.content}>
        <ul className={styles.smallerUl}>
            <li>26 października 1655 – wojska koronne kapitulowały przed Szwedami</li>
            <li>18 listopada – 26 grudnia 1655 – oblężenie Częstochowy przez Szwedów</li>
            <li>1658–1660 – wojna polsko-moskiewska</li>
            <li>3 stycznia 1661 – ukazał się pierwszy numer Merkuriusza Polskiego – pierwszej polskiej - gazety</li>
            <li>10 grudnia 1671 – Turcja wypowiedziała wojnę Rzeczypospolitej</li>
            <li>lipiec – październik 1672 – Turcy najechali na Podole</li>
            <li>11 listopada 1673 – wojska Jana Sobieskiego pokonały Turków pod Chocimiem</li>
            <li>21 maja 1674 – Jan Sobieski został królem Polski</li>
            <li>wrzesień–październik 1676 – Jan III Sobieski pokonał turecko-tatarski najazd na Ruś Czerwoną</li>
            <li>12 września 1683 – bitwa pod Wiedniem: siły koalicji austriacko-niemiecko-polskiej pod dowództwem Jana III Sobieskiego pokonały wojska tureckie</li>
            <li>grudzień 1683 – oddziały polskie i kozackie wyzwoliły Podole</li>
            <li>6 maja 1686 – Rzeczpospolita podpisała z Rosją pokój wieczysty (tzw. traktat Grzymułtowskiego)</li>
            <li>26 stycznia 1699 – Liga Święta zawarła w Karłowicach pokój z Turcją</li>
        </ul>
<div className={styles.img8}></div>
<Link
    smooth={true}
    spy={true}
    to="union9"
    >
    <button>Dalej</button>
</Link>
</div>
</div>
    )
}

export default UniaLubelska8;
