import React, { useEffect } from 'react';
import styles from './UniaLubelska.module.scss';
import { Link } from 'react-scroll';
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

const UniaLubelska9 = () => {
    const wrapper = React.useRef(null);
    const content = React.useRef(null);
    useEffect(() => {
        gsap.registerPlugin(ScrollTrigger);
        const wrap = wrapper.current.children;
        const tl = gsap.timeline();

        tl.fromTo(wrap,0.6,{autoAlpha : 0,x : -100},{autoAlpha : 1,x : 0,scrollTrigger : {
            trigger : '#trigger9',
            start : 'top center',
            id : 'trigger9'
        }})
    });
    return (
        
<div id="trigger9" ref={wrapper} className={styles.union2}>
<h1>Ważne wydarzenia z XVIII w</h1>
<div ref={content} className={styles.content}>
        <ul className={styles.smallerUl}>
            <li>czerwiec – sierpień 1701 – armia szwedzka zajęła Kowno</li>
            <li>24 lipca 1702 – Szwedzi zajęli Warszawę</li>
            <li>19 lipca 1702 – pod Kliszowem Szwecja pokonała armię sasko-polsko-tatarską</li>
            <li>10 sierpnia 1702 – Szwedzi zajęli Kraków</li>
            <li>1705 – August II ustanowił Order Orła Białego</li>
            <li>24 września 1706 – zawarto pokój szwedzko-saski w Altranstäd: </li>
            <li>1 lutego 1717 – odbył się Sejm Niemy – jednodniowe posiedzenie, na którym w obawie przed zerwaniem obrad, nie dopuszczono nikogo do głosu. Obrady sterował rosyjski ambasador zza kulis</li>
            <li>czerwiec – sierpień 1736 – odbył się sejm pacyfikacyjny w Warszawie</li>
            <li>1740 – Stanisław Konarski założył Collegium Nobillium</li>
            <li>19 listopada 1765 – otworzono Teatr Narodowy w Warszawie</li>
            <li>1765 – Stanisław Poniatowski założył Szkołę Rycerską</li>
            <li>1765–1785 – ukazywał się czasopismo społeczno-polityczne „Monitor”</li>
            <li>czerwiec – lipiec 1768 – wybuchło powstanie chłopskie na Ukrainie (koliszczyzna)</li>
            <li>1784–1793 – przebudowa pałacu w Łazienkach</li>

        </ul>
<div className={styles.img9}></div>
<Link
    smooth={true}
    spy={true}
    to="union10"
    >
    <button>Dalej</button>
</Link>
</div>
</div>
    )
}

export default UniaLubelska9;
